.our-product{
    width: 100%;
    height: 100%;
    min-height: 100vh;
position: relative;
   .product-background{
        position: absolute;
        z-index: -1;
        width: 100%;
        height: 100%;
    }
    .product-title{
        position: relative;
               display: flex;
              align-items: flex-end;
               justify-content: space-between;
            
               margin: auto auto;
               width: 90%;
               
               border-bottom: 3px solid $OrangeColor;
                
               h1{
                 
             color: $VioletColor;
                   font-weight: bolder;
                   font-size: 2vw;
          line-height: 10px;
               }
               h2{
                 line-height: 0;
                   color: $VioletColor;
                   border-bottom-color:$OrangeColor ;
                   font-size: 1vw;
}}}


.product-container{
    
width: 100%;
height: 100%;

min-height: 80vh;
display: flex;

 justify-content: space-around;

padding: 50px 150px;
    
}

.product-content{
margin: auto    auto;
   display: flex;
   flex-direction: column;
   align-items: center;
   width: 100%;
   
   h1{
    
       color: $VioletColor;
       font-size:1vw;
   }
   .product-image{
       width: 70%;
       img{
           width: 100%;
       }
   }
    
}
.bottom-border{
    width: 80%;
    margin: auto auto;
    border: 3px dashed $OrangeColor;
}


@media screen and(max-width:1024px){

     .product-title{
         h1{
         font-size: 15px !important;
     }
    h2{
        font-size: 8px !important;
    }
    }
    .product-container{
       padding: 0 ;
  justify-content: space-around;
       

    }
    .product-content{
           
           h1{
            
               color: $VioletColor;
               font-size:10px !important;
           }
           .product-image{
               width: 100%;
               img{
                   width: 100%;
               }
           }
            
        }
}