.about-company{
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    color: red;
    .about-company-title{
        width: 100%;
        text-align: center;
    }
    h1{
        text-align: center;
        margin: 0;
        color: $VioletColor;
        font-size: 2vw;

    }
    .about-company-bg{
        position: absolute;
        z-index: -1;
        height: 100%;
        width: 100%;
        
  
    }
    .about-company-container{
     
        width: 100%;
        // min-height: 100vh;
        position: relative;
     
        justify-content: space-around;
     
        display: flex;
        padding: 0 100px;
        .about-company-image{
            width: 40%;
            img{width: 100%;}
        }
        .about-company-text{
            width: 40%;
            
            padding: 30px 0;
    
            h1{
                color: $VioletColor;
                font-size: 1vw;
                text-align: start
            }
        }
    }
   
}