.services-business{
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 100vh;

    .services-business-bg{
        position: absolute;
        z-index: -1;
        width: 100%;
        height: 100%;
        min-height: 100vh;
    }


}
.services-business-container{
    width: 100%;
    height: 100%;
    position: relative;
padding: 50px 100px;
    .services-business-text{
        
        h1{
            color: $VioletColor;
            font-size: 3.5vw;
            margin: 0;
        }
        h2{
            margin: 0;
            color: $OrangeColor;
            font-size: 2.8vw;
            margin-left: 10vw;
        }
    }
    .services-business-image{
        width: 100%;
     
        img{
           
            width: 100%;}
    }

}
.services-devlopment{
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    text-align: center;
    background-image: url(./assets/images/ServicesImage/ServicesDevBg.png);
    img{
    
        width: 80%;
    }
}