.services-design-bg{
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    text-align: center;
    background-image: url(./assets/images/ServicesImage/ServicesDesignBg.png);
    .services-design-image{
        width: 80%;
    }
}

// second section descriptions the image of design
.services-desgin-description{
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    
    background-image: url(./assets/images/ServicesImage/ServicesDesignBg.png);
    .services-desgin-description-title{
        position: relative;
        display:flex;
        flex-direction: column;
        align-items: center;
        padding-top: 100px;
        h1{
            margin: 0px;
           color: $OrangeColor;
           font-size: 2vw;
        }
        h2{
            margin: 0;
            color: $VioletColor;
            font-size: 1.6vw;
        }
    
    }
.services-desgin-description-desc{
padding: 0 100px;
    p{
        color: $VioletColor;
        font-weight: bold;
        font-size: 1.2vw;
    }
}



}
