
.services-section{
    width: 100%;  
overflow-y: scroll;
   position: relative;
  height:calc( 100% );
min-height: calc(100vh);


 .services-bg{
    width: 100%;

  height: calc(100% / 3);

    position: absolute;
    z-index: -1;
}
}
.services-title{
     padding:  0 100px;
        font-size: 20px;
        text-decoration:underline $OrangeColor ;
        color: $VioletColor;
  
 
}
.services-section::-webkit-scrollbar{
    display: none;
}
.services-container{

    display: flex;
    width: 100%;
    // height: 100%;
    justify-content: space-between;
    padding: 0 100px;





    .services-content{
        width: 50%;
      

        position: relative;
        display: flex;
        flex-direction: column;
        .services-image{
            width: 80%;
         
            img{width: 100%;
           }
        }
        .services-text{
            width: 100%;
            
            
            h2{
            
                font-size: 1vw;
            }
        }
        


    }
    
    .services-right{
        width: 40%;
        display: flex;
    
        .scroll-image{
           
            width: 15%;
            height: 100%;
            min-height: 100vh;
            img{
                height: 100%;
                width: 100%;
            }
          
            }
            .scroll-title{
                width: 100%;
         
          margin: auto auto;
         
         h1{
                    color: $VioletColor;
                    font-size: 3vw;
                    line-height: 2px;
                }
                h2{
                    color: $OrangeColor;
                    line-height: 0;
                    font-size: 1vw;
                }
            
        }
        }
       
}










@media screen and(max-width:1024px) {
    .services-title{
     text-align: center;
     font-size: 15px;
    
   }

    .services-container{
    flex-direction: column-reverse;
    justify-content: center;
  
    padding: 0 10px;

   
    .services-content{
     
        width: 100%;
        height: 100%;
align-items: center;
        position: relative;
        display: flex;
        flex-direction: column;
        .services-image{
            width: 80%;
         
            img{width: 100%;
           }
        }
        .services-text{
            width: 100%;
            
            
            h2{
                // color: $VioletColor;
                font-size: 10px;
            }
        }
}
}


.services-right{
 width: 100% !important;
    display: flex;

    .scroll-image{
    display: none;
      
        }
        .scroll-title{
            width: 100%;
     display: flex;
     align-items: center;
     justify-content: space-between;
      
     
     h1{
                color: $VioletColor;
                font-size: 15px !important;
                line-height: 2px ;
            }
            h2{
                color: $OrangeColor;
                line-height: 0;
                font-size: 10px !important;
            }
        
    }
    }











}